.hero {
  width: 100%;
  height: fit-content;
  margin: 0 auto;

  position: relative;
}

.hero__wrapper {
  display: grid;
  background-color: $color-blue-opacity;
}

.hero__slide {
  position: relative;
  background-size: 100% auto;
  background-repeat: no-repeat;
  background-color: $black-to-right;

  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;

  padding: 180px 0 80px 0;
  width: 100%;
  height: fit-content;
  box-sizing: content-box;
  margin: 0 auto;

  @include vp-1199 {
    padding: 173px 0 70px 0;
    width: 100%;

    justify-content: center;
    align-items: center;
  }

  @include vp-767 {
    padding: 139px 0 30px 0;

    br {
      display: none;
    }
  }

  &--caucausus {
    background-image: url("../../../img/caucausus-desktop@1x.jpg");

    @include vp-1199 {
      background-image: url("../../../img/caucausus-tablet@1x.jpg");
    }

    @include vp-767 {
      background-image: url("../../../img/caucausus-mobile@1x.jpg");
    }

    @include retina {
      background-image: url("../../../img/caucausus-desktop@2x.jpg");

      @include vp-1199 {
        background-image: url("../../../img/caucausus-tablet@2x.jpg");
      }

      @include vp-767 {
        background-image: url("../../../img/caucausus-mobile@2x.jpg");
      }
    }
  }

  &--kamchatka {
    background-image: url("../../../img/kamchatka-desktop@1x.jpg");

    @include vp-1199 {
      background-image: url("../../../img/kamchatka-tablet@1x.jpg");
    }

    @include vp-767 {
      background-image: url("../../../img/kamchatka-mobile@1x.jpg");
    }

    @include retina {
      background-image: url("../../../img/kamchatka-desktop@2x.jpg");

      @include vp-1199 {
        background-image: url("../../../img/kamchatka-tablet@2x.jpg");
      }

      @include vp-767 {
        background-image: url("../../../img/kamchatka-mobile@1x.jpg");
      }
    }
  }

  &--altay {
    background-image: url("../../../img/altay-desktop@1x.jpg");

    @include vp-1199 {
      background-image: url("../../../img/altay-tablet@1x.jpg");
    }

    @include vp-767 {
      background-image: url("../../../img/altay-mobile@1x.jpg");
    }

    @include retina {
      background-image: url("../../../img/altay-desktop@2x.jpg");

      @include vp-1199 {
        background-image: url("../../../img/altay-tablet@2x.jpg");
      }

      @include vp-767 {
        background-image: url("../../../img/altay-mobile@1x.jpg");
      }
    }
  }
}

.hero__slide-wrapper {
  margin: 0 auto;
  width: 1200px;

  @include vp-1199 {
    width: 678px;

    display: flex;
    flex-direction: column;
  }

  @include vp-767 {
    width: 290px;
  }
}

.hero__slide h2 {
  position: relative;
  font-size: 77px;
  font-style: normal;
  font-weight: 800;
  line-height: 77px;

  padding: 0 0 25px 0;
  margin: 0;

  color: $color-default-white;
  z-index: 3;

  @include vp-1199 {
    font-size: 56px;
    line-height: 56px;
    text-align: center;

    padding: 0 0 15px 0;
  }

  @include vp-767 {
    font-size: 30px;
    line-height: 30px;
  }
}

.hero__slide p {
  font-size: 17px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;

  padding: 0 0 37px 0;
  margin: 0;

  color: $color-default-white;

  @include vp-1199 {
    font-size: 17px;
    line-height: 24px; 
    text-align: center;

    padding: 0 0 25px 0;
  }

  @include vp-767 {
    font-size: 15px;
    line-height: 20px;
    padding: 0 0 20px 0;
  }
}

.hero__slide-details {
  display: flex;
  gap: 10px;

  @include vp-1199 {
    display: none;
  }
}

.hero__slide-details-block {
  padding: 13px 19px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;

  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 16px; 

  color: $color-default-white;

  width: max-content;

  border: 1px solid $color-white-opacity;
}

.hero__button {
  width: 220px;

  margin: 0 0 150px 0;

  @include vp-1199 {
    width: 206px;
    margin: 0 auto 50px auto;
  }

  @include vp-767 {
    width: 180px;
    padding: 18px 0;
    margin: 0 auto 142px auto;

    @include hover-focus {
      padding: 17px 0;
    }
  }
}

.hero .hero__pagination {
  display: flex;
  justify-content: flex-end;
  gap: 5px;
  position: absolute;
  left: auto;
  right: 120px;
  bottom: 95px;

  margin: 0;

  z-index: 2;

  @include vp-1199 {
    justify-content: center;
    right: 0;
    bottom: 90px;
    gap: 6px;
  }

  @include vp-767 {
    bottom: 40px;
    gap: 4px;
  }
}

.hero .swiper-pagination-bullet {
  min-width: 10px;
  min-height: 10px;
  background: transparent;
  border-radius: 50%;
  border: 1px solid $color-default-white;
  opacity: 1;
  margin: 0;

  @include vp-767 {
    min-width: 8px;
    min-height: 8px;
  }

  @include hover-focus {
    opacity: 0.7;
  }

  @include active {
    opacity: 0.9;
    background-color: $color-default-white;
  }
}

.hero .swiper-pagination-bullet-active {
  background: $color-default-white;
}

.hero__slide--kamchatka {
  .hero__button {
    @include vp-767 {
      margin-bottom: 122px;
    }
  }
}

.hero__slide--altay {
  .hero__button {
    @include vp-767 {
      margin-bottom: 122px;
    }
  }
}